@keyframes bounce {
  from { top: 0; }
  to { top: -0.5rem; }
}

.map {
  position: relative;
  display: flex;
  flex: 1;
  min-height: 100px;
}

.map.with-crosshair::before {
  content: '\0020';
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  height: 40px;
  width: 3px;
  margin-top: -20px;
  margin-left: -1px;
  background: rgba(0, 0, 0, 0.8);
}

.map.with-crosshair::after {
  content: '\0020';
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  height: 3px;
  width: 40px;
  margin-top: -1px;
  margin-left: -20px;
  background: rgba(0, 0, 0, 0.8);
}

.index {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  padding-top: 1px;
  height: 100%;
  width: 100%;
  color: #fff;
  text-align: center;
  font-weight: bold;
  font-size: 1rem;
}

.markerHighlighted {
  position: relative;
  animation: bounce 0.5s infinite alternate;
}

.locate {
  display: block;
  height: 30px;
  background: center 2px no-repeat;

  /* ../../images/where-i-am.png */
  background-image: url('data:image/png;base64,');
}

.loading {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.8);
}

:global(.leaflet-div-icon) {
  border: 0 !important;
  background: none !important;
}

/* Cluster markers */

.cluster {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 50px;
  width: 50px;
  margin: -25px 0 0 -25px; /* Origin center */
  padding-bottom: 6px;
  border: 0;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  font-size: 0.75rem;
  font-weight: bold;
  line-height: 1;
  background-color: #1a1a1a;
}

.count {
  display: block;
  font-weight: normal;
  font-size: 1.25rem;
}

/* Override control styles */

:global(.leaflet-bar) {
  box-shadow: none;
  border: 1px solid #ccc;
}

/* Override geocode control vendor styles */

:global(.leaflet-control-geocoder-icon) {
  position: static;
  height: 34px !important; /* Matches input field height */
  width: 34px !important;
  padding: 0 1rem;

  /* ../../images/geocoder.png */
  background-image: url('data:image/png;base64,') !important;
  cursor: pointer;
}

:global(.leaflet-bar a) {
  height: 30px !important;
  width: 30px !important;
}

:global(.leaflet-control-geocoder-throbber .leaflet-control-geocoder-icon) {
  /* ../../images/throbber.gif */
  background-image: url('data:image/gif;base64,');
}
